





















































@import "../assets/scss/base/variables.scss";
@import "../assets/scss/base/mixins.scss";

.logo404{
    text-align: center;
    margin-top: 50px;
}
.not-found-block{
    max-width: 888px;
    margin: 0 auto;
}

.thank-subtitle {
    margin-bottom: 48px;
    font-family: $main-font;
    font-size: 18px;
    line-height: 24px;
    @media all and (min-width: 960px) {
        margin-bottom: 80px;
        line-height: 28px;
    }
}



.button-404{
    height: 60px;
    border-radius: 8px;
    margin: 0 auto 16px;
    font-family: $additional-font;
    font-weight: 400;
    font-size: 24px;
    background-color: #98ca3e;
    color: #221f44;
    max-width: 264px;
    align-items:center;
    display:flex;
    flex:1 0 auto;
    justify-content:center;
    position:relative;
    transition: .5s all;
    text-decoration: none;
    &:hover {
        background-color: #E8E6FF;
    }
}






