    @media all and (max-width: 520px) {
        .small-label-tabel {
            max-width: 100% !important;
        }
    }

    .share-wrap {
        margin: auto 0 50px !important;
        justify-content: center;
        border-radius: 0 0 8px 8px;
        background-color: #464085;

        .share_result {
            padding: 20px 0;
            color: #98ca3e;
            font-family: $additional-font;
            font-size: 18px;
            font-weight: 400;
            letter-spacing: 2px;
            line-height: 1.33;
            text-transform: uppercase;
            display: flex;
            outline: none;
            text-decoration: none;

            &:before {
                content: url(../assets/images/share_icon.png);
                padding-right: 20px;
                display: inline-block;
                height: 19px;
            }
        }

        @media (min-width: 768px) {
            margin: auto 0 8.6vw !important;
        }
        @media (min-width: 1921px) {
            margin: auto 0 165px !important;
        }
    }

    .result-table-wrap {
        border-radius: 8px 8px 0 0;
        background-color: #f6f5ff;
        @media (max-width: 768px) {
            overflow: hidden;
        }

        .result-table-head {
            margin: auto 0 !important;
            border-radius: 8px 8px 0 0;
            background-color: #e8e6ff;

            .table_layout_head {
                height: inherit;
                padding: 0;
            }

            .table-header {
                padding: 8px 0 30px;

                .table-choice {
                    text-align: center;

                    &-item {
                        color: #6130f0;
                        font-family: $main-font;
                        font-size: 16px;
                        font-weight: 700;
                        line-height: 24px;
                    }
                }

                .table-image-wrap {
                    text-align: center;
                    max-width: 100px;
                    margin: auto;
                    padding-top: 20px;

                    img {
                        max-width: 100%;
                        height: auto;
                    }
                }
            }

            @media all and (min-width: 768px) {
                .empty-head {
                    max-width: 300px;
                    border-right: 1px solid rgba(97, 48, 240, 0.2);
                }
                border-bottom: 1px solid rgba(97, 48, 240, 0.2);
                .table-image-wrap {
                    max-width: 140px;
                }
            }
        }

        .result-table-body {
            margin: auto 0 !important;
            width: 100%;

            .table_title-block {
                padding: 0;

                .compTitle {
                    margin-right: -1px;
                    padding: 12px 13px 12px 16px;
                    color: #110a29;
                    font-family: $main-font;
                    font-size: 16px;
                    line-height: 24px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    &:not(:last-child) {
                        border-bottom: 1px solid rgba(97, 48, 240, 0.2);
                    }
                }

                @media all and (max-width: 768px) {
                    box-shadow: 20px 0 20px -20px rgba(0, 0, 0, 0.1);
                }
                @media all and (min-width: 768px) {
                    max-width: 300px;
                    .compTitle {
                        border-right: 1px solid rgba(97, 48, 240, 0.2);
                        padding: 12px 0 12px 24px;
                    }
                }
            }
        }

        .table_layout {
            padding: 0 !important;

            .icon-block {
                color: #6130f0;
                font-family: $main-font;
                font-size: 14px;
                font-weight: 400;
                padding: 12px 0;
                min-height: 49px;
                display: flex;
                justify-content: center;
                align-items: center;

                &:not(:last-child) {
                    border-bottom: 1px solid rgba(97, 48, 240, 0.2);
                }
            }

            &:not(:last-child) {
                border-right: 1px solid rgba(97, 48, 240, 0.2);
            }
        }

    }




    /* Modals
----------------------------------------------*/
    [data-ml-modal] {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        overflow-x: hidden;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        z-index: 999;
        width: 0;
        height: 0;
        opacity: 0;
    }

    [data-ml-modal]:target {
        width: auto;
        height: auto;
        opacity: 1;
        -webkit-transition: opacity 1s ease;
        transition: opacity 1s ease;
    }

    [data-ml-modal]:target .modal-overlay {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        cursor: pointer;
        background-color: #000;
        background-color: rgba(0, 0, 0, 0.7);
        z-index: 1;
    }

    [data-ml-modal] .modal-dialog {
        border-radius: 6px;
        box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
        position: relative;
        width: 90%;
        max-width: 660px;
        max-height: 70%;
        margin: 10% auto;
        overflow-x: hidden;
        overflow-y: auto;
        z-index: 2;
    }

    .modal-dialog-lg {
        max-width: 820px !important;
    }

    [data-ml-modal] .modal-dialog > h3 {
        background-color: #eee;
        border-bottom: 1px solid #b3b3b3;
        font-size: 24px;
        font-weight: 400;
        margin: 0;
        padding: 0.8em 56px .8em 27px;
    }

    [data-ml-modal] .modal-content {
        background: #fff;
        padding: 23px 27px;
    }

    [data-ml-modal] .modal-close {
        position: absolute;
        top: 13px;
        right: 13px;
        color: #0085a6;
        background-color: #fff;
        border-radius: 50%;
        height: 40px;
        width: 40px;
        font-size: 30px;
        line-height: 37px;
        text-align: center;
        -webkit-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out;
    }

    [data-ml-modal] .modal-close:hover {
        background-color: #0085a6;
        color: #fff;
        cursor: pointer;
    }

    [data-ml-modal] p:first-child, [data-ml-modal] p:last-child {
        margin: 0;
    }

    @media (max-width: 767px) {
        [data-ml-modal] .modal-dialog {
            margin: 20% auto;
        }
    }

    /* End Modals ------------------------------*/

