    padding: 32px 24px;
    min-height: 148px;
}

.quiz-label3 {
    padding: 36px 24px;
}

.quiz-label5 {
    @media all and (min-width: 960px) {
        width: 204px;
        padding: 15px 24px;
        min-height: 128px;
    }
}

.main-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

